
import Swiper from 'swiper';
const nextButton = document.querySelector('.project-swiper-button-next');
const prevButton = document.querySelector('.project-swiper-button-prev');

nextButton.addEventListener('click', () => {
  swiper.slideNext();
});

prevButton.addEventListener('click', () => {
  swiper.slidePrev();
});


const swiper = new Swiper('.project-swiper-container', {
  slidesPerView: 1, 
  spaceBetween: 30,
  pagination: {
    el: '.project-swiper-pagination',
    clickable: true,
  },
  navigation: {
    nextEl: '.project-swiper-button-left',
    prevEl: '.project-swiper-button-right',
  },
});
